/* src/components/Header.css */

.header-container {
  background-color: #2563eb; /* bg-blue-700 */
  color: white; /* 기본 텍스트 색상을 흰색으로 설정 (h1에 이미 적용됨) */
  padding: 1.5rem 1rem; /* py-6 px-4 */
  text-align: center;
}

.header-title {
  font-size: 2.25rem; /* 예: 1.875rem에서 증가 */
  line-height: 2.5rem;
  font-weight: 700; /* font-bold */
  color: white; /* 명시적으로 흰색 지정 (부모로부터 상속받지만 확실히 하기 위해) */
}

.header-nav {
  margin-top: 0.5rem; /* mt-2 */
  font-size: 1.25rem; 
  line-height: 1.75rem;
}

.header-nav-link {
  text-decoration: underline;
  margin-right: 1rem; /* mr-4 (for the first link) */
  color: white; /* 링크 텍스트 색상을 흰색으로 변경 */
  transition: color 0.15s ease-in-out; /* 색상 변경 시 부드러운 전환 효과 */
}

.header-nav-link:hover {
  color: #fefcbf; /* hover:text-yellow-200 (연한 노란색) */
  /* 또는 다른 밝은 색상: color: #e0e0e0; (밝은 회색) */
}

.header-welcome-message {
  color: white;
  margin-right: 1rem;
  font-weight: 500;
}

.header-logout-button {
  background-color: transparent;
  border: 1px solid white;
  color: white;
  padding: 0.3rem 0.8rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9em; /* 링크와 비슷한 크기 */
  transition: background-color 0.15s ease-in-out, color 0.15s ease-in-out;
}

.header-logout-button:hover {
  background-color: white;
  color: #2563eb; /* 헤더 배경색과 유사하게 */
}